.searchBox {
  font-size: 14px;
  background-color:#fff;
}

.searchBox__container {
  height: 45px;
  color: #f63;
  padding-left: 8px;
  display: flex;
  align-items: center;
}

.searchBox__text {
  background-color: #f0f0f0;
  width: calc(100% - 68px);
  height: 30px;
  padding-left: 10px;
  padding-right: 30px;
  border: none;
  border-radius: 15px;
}

.searchBox__clear {
  background-image: url("https://www.dpfile.com/app/node-mobile-m-isomorphism-web/static/d53930bbff0bf22cb8cea72503da44d9.png");
  background-size: 250px;
  background-repeat: no-repeat;
  background-position: 3px -185px;
  width: 22px;
  height: 34px;
  margin-left: -25px;
}

.searchBox__cancel {
  width: 48px;
  height: 45px;
  line-height: 45px;
  padding: 0 10px;
  display: inline-block;
  font-size: 14px;
  text-align: center;
}

.searchBox__list {
  position: fixed;
  width: 100%;
  height: calc(100% - 45px);
  box-sizing: border-box;
  top: 45px;
  list-style: none;
  padding: 0 10px 0 15px;
  margin: 0;
  z-index: 9999;
  background-color: #fff;
}

.searchBox__item {
  padding: 15px 10px 15px 0;
  font-size: 15px;
  line-height: 15px;
  position: relative;
  display: block;
  color: #333;
  overflow: hidden;
}

.searchBox__itemTitle {
  float: left;
  color: #666;
}

.searchBox__itemQuantity {
  float: right;
  color: #666;
  font-size: 12px;
}

.searchHistory {
  background-color: #fff;
  font-size: 15px;
  color: #323232;
  margin-top: 1px;
}

.searchHistory__header {
  background-color: #f0f0f0;
  padding: 8px 15px;
  color: #999;
}

.searchHistory__list {
  padding-left: 15px;
  margin: 0;
}

.searchHistory__item {
  display: block;
  color: #323232;
  height: 48px;
  line-height: 48px;
  background-color: #fff;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  background-image: linear-gradient(0, #e1e1e1, #e1e1e1 50%, transparent 50%);
  background-position: 100% 100%;
  background-size: 100% 1px;
  background-repeat: no-repeat;
}

.searchHistory__clear {
  height: 45px;
  line-height: 45px;
  color: #333;
  text-align: center;
  font-size: 15px;
  background-color: #fff;
  display: block;
  border: none;
}

.popularSearch {
  padding: 10px 15px;
  font-size: 14px;
  background-color: #f0f0f0;
}

.popularSearch::after {
  display: block;
  content: "";
  height: 0;
  clear: both;
}

.popularSearch__item {
  float: left;
  background-color: #fff !important;
  color: #323232;
  width: 33%;
  height: 38px;
  line-height: 38px;
  text-align: center;
  padding: 0;
  margin: 0;
}

.popularSearch__item:nth-child(1),
.popularSearch__item:nth-child(2),
.popularSearch__item:nth-child(4),
.popularSearch__item:nth-child(5) {
  background-image: linear-gradient(0, #e1e1e1, #e1e1e1 50%, transparent 50%),
    linear-gradient(90deg, #e1e1e1, #e1e1e1 50%, transparent 50%);
  background-position-x: right, right;
  background-position-y: bottom, top;
  background-size: 100% 1px, 1px 100%;
  background-repeat: no-repeat;
}

.popularSearch__item:nth-child(7),
.popularSearch__item:nth-child(8) {
  background-image: linear-gradient(90deg,#e1e1e1,#e1e1e1 50%,transparent 50%);
  background-position: top right;
  background-size: 1px 100%;
  background-repeat: no-repeat;
}

